    box-shadow: none;
    border-radius: 10px;
    padding: 0.6rem 1rem;
  }
.btn-primary:hover {
    background-color: var(--color-primary);
    transform: translateY(-1px);
    box-shadow: 0 8px 30px rgba(59,110,246,0.06);
  }
