    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    width: 10px;
    height: 10px;
    transform: translate(-50%, -50%) scale(0);
    border-radius: 9999px;
    background: radial-gradient(circle, rgba(91,140,255,0.95) 0%, rgba(91,140,255,0.2) 40%, rgba(91,140,255,0.0) 70%);
