:root {
  /* Typography (Notion-like) */
  --font-sans: 'Inter', system-ui, Avenir, Segoe UI, Helvetica, Arial, sans-serif;
  --font-weight-regular: 400;
  --font-weight-medium: 600;
  --font-weight-bold: 800;

  /* Core colors */
  --color-primary: #5b8cff; /* soft ink */
  --color-primary-600: #3b6ef6;
  --color-accent: #ff6b9f;
  --color-cyan: #06b6d4;

  /* Pen & paper light theme tokens */
  --color-bg: #FBF9F7; /* very light warm paper */
  --color-surface: #FFFFFF; /* cards / surfaces */
  --color-text: #0F172A; /* primary ink color */
  --color-muted: #6b7280; /* soft muted text */

  /* Layout & texture */
  --paper-line: linear-gradient(rgba(0,0,0,0.015) 1px, transparent 1px);

  color-scheme: light dark;
  color: var(--color-text);
  background-color: var(--color-bg);
  background-image: var(--paper-line);
  background-size: 100% 56px;

  font-family: var(--font-sans);
  font-weight: var(--font-weight-regular);
  line-height: 1.6;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Dark mode token overrides (activated via `class="dark"` on <html>) */
.dark :root {
  --color-bg: #071023; /* deep navy - keeps the same 'paper' feeling */
  --color-surface: #0A0C10;
  --color-text: #F8FAFF; /* brighter light ink for dark */
  --color-muted: #9fb2c8; /* slightly higher contrast muted text */

  /* disable paper-lines in dark for clarity */
  background-image: none;
}


a {
  font-weight: 500;
  color: #646cff;
  text-decoration: inherit;
}
a:hover {
  color: #535bf2;
}

body {
  margin: 0;
  min-width: 320px;
  min-height: 100vh;
  font-family: var(--font-sans);
  background-color: var(--color-bg);
  color: var(--color-text);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Make main content feel like paper cards */
.container, .max-w-7xl, .max-w-6xl {
  padding-left: 1rem;
  padding-right: 1rem;
}

.card, .surface {
  background: var(--color-surface);
  border-radius: 14px;
  box-shadow: 0 6px 24px rgba(16,24,40,0.04);
  padding: 1rem;
}

h1, h2, h3 {
  font-family: var(--font-sans);
  color: var(--color-text);
  margin: 0 0 0.4rem 0;
  letter-spacing: -0.02em;
}

h1 {
  font-size: 2.25rem; /* responsive sizes are handled in tailwind classes too */
  font-weight: 800;
  line-height: 1.05;
}

h2 {
  font-size: 1.5rem;
  font-weight: 700;
}

p {
  color: var(--color-muted);
  font-size: 1rem;
  line-height: 1.7;
}

button {
  border-radius: 0.75rem;
  border: 1px solid transparent;
  padding: 0.6em 1.2em;
  font-size: 1rem;
  font-weight: 600;
  font-family: var(--font-sans);
  background-color: transparent;
  cursor: pointer;
  transition: transform 0.15s ease, box-shadow 0.15s ease, background-color 0.15s ease;
  color: inherit;
}
button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 30px rgba(16,24,40,0.06);
}
button:focus,
button:focus-visible {
  outline: 3px solid rgba(99,102,241,0.14);
  outline-offset: 2px;
  border-color: rgba(99,102,241,0.12);
}

/* Theme variants - add class to <html> like `class="theme-modern"` or `class="theme-paper"` */
.theme-modern :root {
  --color-bg: #ffffff;
  --color-surface: #f8fafc;
  --color-text: #0F172A;
  --color-muted: #6b7280;
  --color-primary: #6366f1;
  --color-primary-600: #4f46e5;
  background-image: none;
}
.theme-modern.dark :root {
  --color-bg: #061128;
  --color-surface: #071027;
  --color-text: #F8FAFF;
  --color-muted: #9fb2c8;
}

.theme-paper :root {
  /* same as paper defaults */
  --color-bg: #FBF9F7;
  --color-surface: #FFFFFF;
  --color-text: #0F172A;
  --color-muted: #6b7280;
  background-image: var(--paper-line);
}
.theme-paper.dark :root {
  --color-bg: #071023;
  --color-surface: #0A0C10;
  --color-text: #F8FAFF;
  --color-muted: #9fb2c8;
  background-image: none;
}

@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #ffffff;
  }
  a:hover {
    color: #747bff;
  }
  button {
    background-color: transparent;
  }
}

